import React, { createContext, useContext, useState, useEffect } from 'react';
import {CartContext} from '../context/index'

export const CartProvider = ({ children }) => {
    const [cartItems, setCartItems] = useState(() => {
        const storedCart = localStorage.getItem('cart');
        return storedCart ? JSON.parse(storedCart) : [];
    });

    const addToCart = (product, quantity = 1) => {
        setCartItems(prevCartItems => {
            const existingItem = prevCartItems.find(item => item.id === product.id);

            if (existingItem) {
                const newQuantity = existingItem.quantity + quantity;
                // Если новое количество больше 0, обновляем количество
                // Иначе удаляем товар из корзины
                return newQuantity > 0
                    ? prevCartItems.map(item => (item.id === product.id ? { ...item, quantity: newQuantity } : item))
                    : prevCartItems.filter(item => item.id !== product.id);
            } else {
                // Если товара нет в корзине, добавляем целиком
                return [...prevCartItems, { ...product, quantity }];
            }
        });
    };


    const removeFromCart = (productId) => {
        setCartItems(prevCartItems =>
            prevCartItems.map(item =>
                item.id === productId ? { ...item, quantity: Math.max(item.quantity - 1, 0) } : item
            ).filter(item => item.quantity > 0)
        );
    };

    const clearCart = () => {
        setCartItems([]);
    };

    // Сохраняем корзину в localStorage при изменении
    useEffect(() => {
        localStorage.setItem('cart', JSON.stringify(cartItems));
    }, [cartItems]);

    const contextValue = {
        cartItems,
        addToCart,
        removeFromCart,
        clearCart,
    };

    return (
        <CartContext.Provider value={contextValue}>
            {children}
        </CartContext.Provider>
    );
};
