import React, { useContext, useState } from 'react';
import { Button, Col, Form, Row } from 'react-bootstrap';
import Container from "react-bootstrap/Container";
import { Link } from "react-router-dom";
import { CartContext } from "../context";
import './css/OrderPage.css'

export const OrderPage = () => {
    const [formType, setFormType] = useState('individual');
    const [formTypeDelivery, setFormTypeDelivery] = useState('solo');
    const [formData, setFormData] = useState({
        individual: {
            name: '',
            phone: '',
            email: '',
        },
        legal: {
            inn: '',
            organization: '',
            phone: '',
            legalName: '',
        },
        delivery: {
            address: '',
            date: '',
        }
    });
    const { cartItems, addToCart } = useContext(CartContext);

    const calculateTotal = () => {
        const totalItems = cartItems.reduce((total, item) => total + item.quantity, 0);
        const totalPrice = cartItems.reduce((total, item) => total + item.amount * item.quantity, 0);
        return { totalItems, totalPrice };
    };

    const { totalItems, totalPrice } = calculateTotal();

    const handleFormTypeChange = (type) => {
        setFormType(type);
    };

    const handleFormTypeChangeDelivery = (type) => {
        setFormTypeDelivery(type);
    };

    const handleInputChange = (e, type, field) => {
        setFormData(prevData => ({
            ...prevData,
            [type]: {
                ...prevData[type],
                [field]: e.target.value,
            },
        }));
    };

    const handleFormSubmit = async (e) => {
        e.preventDefault();

        // Теперь данные из формы доступны в объекте formData
        console.log(formData);
    };

    const styleIndividual = {
        border: formType === 'individual' ? '2px solid red' : '2px solid gray',
        background: 'none',
        color: 'black',
    };

    const styleLegal = {
        border: formType === 'legal' ? '2px solid red' : '2px solid gray',
        background: 'none',
        color: 'black',
    };

    const styleSolo = {
        border: formTypeDelivery === 'solo' ? '2px solid red' : '2px solid gray',
        background: 'none',
        color: 'black',
    };

    const styleDelivery = {
        border: formTypeDelivery === 'delivery' ? '2px solid red' : '2px solid gray',
        background: 'none',
        color: 'black',
    };

    return (
        <Container fluid>
            <div className='containerOrderForm'>
                <Button
                    onClick={() => handleFormTypeChange('individual')}
                    style={styleIndividual}
                >
                    Физическое лицо
                </Button>{' '}
                <Button
                    onClick={() => handleFormTypeChange('legal')}
                    style={styleLegal}
                >
                    Юридическое лицо
                </Button>
                <Row>
                    {formType === 'individual' && (
                        <Form onSubmit={handleFormSubmit}>
                            <Form.Group controlId="individualFormName">
                                <Form.Label>ФИО</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Введите Ваше имя"
                                    value={formData.individual.name}
                                    onChange={(e) => handleInputChange(e, 'individual', 'name')}
                                />
                            </Form.Group>
                            <Form.Group controlId="individualFormPhone">
                                <Form.Label>Телефон</Form.Label>
                                <Form.Control
                                    type="tel"
                                    placeholder="Введите Ваш телефон"
                                    value={formData.individual.phone}
                                    onChange={(e) => handleInputChange(e, 'individual', 'phone')}
                                />
                            </Form.Group>
                            <Form.Group controlId="individualFormEmail">
                                <Form.Label>Почта</Form.Label>
                                <Form.Control
                                    type="email"
                                    placeholder="Введите Вашу почту"
                                    value={formData.individual.email}
                                    onChange={(e) => handleInputChange(e, 'individual', 'email')}
                                />
                            </Form.Group>
                        </Form>
                    )}
                </Row>
                <Row>
                    {formType === 'legal' && (
                        <Form onSubmit={handleFormSubmit}>
                            <Form.Group controlId="legalFormINN">
                                <Form.Label>ИНН</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Введите ИНН"
                                    value={formData.legal.inn}
                                    onChange={(e) => handleInputChange(e, 'legal', 'inn')}
                                />
                            </Form.Group>

                            <Form.Group controlId="legalFormOrganization">
                                <Form.Label>Организация</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Введите название организации"
                                    value={formData.legal.organization}
                                    onChange={(e) => handleInputChange(e, 'legal', 'organization')}
                                />
                            </Form.Group>

                            <Form.Group controlId="legalFormPhone">
                                <Form.Label>Телефон</Form.Label>
                                <Form.Control
                                    type="tel"
                                    placeholder="Введите телефон"
                                    value={formData.legal.phone}
                                    onChange={(e) => handleInputChange(e, 'legal', 'phone')}
                                />
                            </Form.Group>

                            <Form.Group controlId="legalFormName">
                                <Form.Label>ФИО</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Введите Ваше имя"
                                    value={formData.legal.legalName}
                                    onChange={(e) => handleInputChange(e, 'legal', 'legalName')}
                                />
                            </Form.Group>

                            {/* Добавьте дополнительные поля по необходимости */}
                        </Form>
                    )}
                </Row>
            <div style={{marginTop: '20px'}}>
                <h4><b>Способ доставки</b></h4>
                <br/>
                <Button
                    onClick={() => handleFormTypeChangeDelivery('solo')}
                    style={styleSolo}
                >
                    Самовывоз
                </Button>{' '}
                <Button
                    onClick={() => handleFormTypeChangeDelivery('delivery')}
                    style={styleDelivery}
                >
                    Курьер
                </Button>

                {formTypeDelivery === 'solo' && (
                    <Form onSubmit={handleFormSubmit}>
                        Забирай где хочешь, черт.

                    </Form>
                )}


                {formTypeDelivery === 'delivery' && (
                    <Form onSubmit={handleFormSubmit}>
                        <Form.Group controlId="deliveryFormAddress">
                            <Form.Label>Адрес</Form.Label>
                            <Form.Control
                                type="text"
                                placeholder="Введите адрес"
                                value={formData.delivery.address}
                                onChange={(e) => handleInputChange(e, 'delivery', 'address')}
                            />
                        </Form.Group>

                        <Form.Group controlId="deliveryFormDate">
                            <Form.Label>Дата</Form.Label>
                            <Form.Control
                                type="date"
                                name="deliveryDate"
                                placeholder="Менеджер уточнит время доставки."
                                value={formData.delivery.date}
                                onChange={(e) => handleInputChange(e, 'delivery', 'date')}
                            />
                        </Form.Group>
                    </Form>
                )}
            </div>
        </div>
        <Col xs={12} md={4} style={{ display: 'flex', justifyContent: 'end', float: 'right'}}>
            <div style={{ background: 'var(--main-pink)', maxWidth: '350px', padding: '5%', height: 'fit-content', borderRadius: '5%' }}  className="cartInfoPC">
                <h3>Ваша корзина</h3>
                <p>{totalItems.toLocaleString()} товаров</p>
                <hr />
                <p><b>ИТОГО: {totalPrice.toLocaleString()} ₽</b></p>
                <Link to={`/order`} style={{
                    color: "black",
                }}>
                    <Button
                        variant="primary"
                        style={{
                            background: 'var(--main-red)',
                            width: '100%',
                            height: '42px'
                        }}
                        onClick={handleFormSubmit}

                    >
                        Подтвердить заказ
                    </Button>
                </Link>
                <hr />
                <p style={{ fontSize: '14px', color: '#909090'}}>
                    Нажимая на кнопку, Вы соглашаетесь с Условиями обработки персональных данных, а также с Условиями продажи.
                </p>
            </div>
        </Col>
    </Container>
    );
};

