import axios from 'axios';
import {BASE_URL} from "../../service/settings";

export class NewProductApi {
    BASE_URL = BASE_URL
    get({
            product_id = null,
            name = null,
            subcategory_id = null,
            subcategory_name = null,
            category_id = null,
            category_name = null,
            type_id = null,
            type_name = null,
            manufacturer_id = null,
            manufacturer_name = null,
            limit = 10000,
            offset = 0
        }) {

        const url = this.BASE_URL + '/new'
        return axios.get(url, {
            params: {
                id: product_id,
                name: name,
                subcategory_id: subcategory_id,
                subcategory_name: subcategory_name,
                category_id: category_id,
                category_name: category_name,
                type_id: type_id,
                type_name: type_name,
                manufacturer_id: manufacturer_id,
                manufacturer_name: manufacturer_name,
                limit: limit,
                offset: offset,
            }
        })

            .then(response => response.data )
            .catch(error => {
                // Обработка ошибок
                console.error('Ошибка при выполнении запроса:', error);
                throw error;
            });
    }

    post(productData) {
        const url = this.BASE_URL + '/new';
        return axios.post(url, productData)
            .then(response => response.data)
            .catch(error => {
                // Обработка ошибок
                console.error('Ошибка при выполнении запроса:', error);
                throw error;
            });
    }

    put(productData) {
        const url = this.BASE_URL + `/new`;
        return axios.put(url, productData)
            .then(response => response.data)
            .catch(error => {
                console.error('Ошибка при выполнении запроса:', error);
                throw error;
            });
    }
    delete(product_id) {
        const url = this.BASE_URL + `/new`;
        return axios.delete(
            url,
            {
                params: {
                    id: product_id,
                }
            }
            )
            .then(response => response.data)
            .catch(error => {
                console.error('Ошибка при выполнении запроса:', error);
                throw error;
            });
    }

}