import React, { useContext } from "react";
import { CartContext } from "../context";
import { Container, Row, Col, Card, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { Cart } from "../components/Service/Cart/Cart";
import {ToCart} from "../components/Cart/ToCart";

export const CartPage = () => {
    const { cartItems, addToCart } = useContext(CartContext);

    const calculateTotal = () => {
        const totalItems = cartItems.reduce((total, item) => total + item.quantity, 0);
        const totalPrice = cartItems.reduce((total, item) => total + item.amount * item.quantity, 0);
        return { totalItems, totalPrice };
    };
    const { totalItems, totalPrice } = calculateTotal();

    return (
        <Container fluid style={{ margin: '0 auto' }}>
            <Row>
                <Col xs={12} md={8}>
                    <Row>
                        {cartItems.map(product => (
                            <Col key={product.id} xs={6} sm={6} md={6} lg={3}>
                                <Card className="mb-3">
                                    <Link to={`/catalog/detail/${product.subcategory.name}/${product.id}`} style={{ color: "black" }}>
                                        <Card.Img variant="top" src={product.images[0]} style={{ maxHeight: '250px', objectFit: 'cover' }} />
                                    </Link>
                                    <ToCart product={product}/>
                                </Card>
                            </Col>
                        ))}
                    </Row>
                </Col>
                <Col xs={12} md={4} style={{ display: 'flex', justifyContent: 'end' }}>
                    <div style={{ background: 'var(--main-pink)', maxWidth: '350px', padding: '5%', height: 'fit-content', borderRadius: '5%' }}>
                        <h3>Ваша корзина</h3>
                        <p>{totalItems.toLocaleString()} товаров</p>
                        <hr />
                        <p><b>ИТОГО: {totalPrice.toLocaleString()} ₽</b></p>
                        <Link to={`/order`} style={{
                            color: "black",
                        }}>
                            <Button
                                variant="primary"
                                style={{
                                    background: 'var(--main-red)',
                                    width: '100%',
                                    height: '42px'
                                }}>
                                Оформить заказ
                            </Button>
                        </Link>
                        <hr />
                        <p style={{ fontSize: '14px', color: '#909090'}}>
                            Доступные способы доставки и оплаты можно выбрать при оформлении заказа
                        </p>
                    </div>
                </Col>

            </Row>
        </Container>
    );
};
