import React, { useState, useEffect } from 'react';
import { Carousel } from 'react-bootstrap';
import './Slider.module.css';

const Slider = () => {
    const [index, setIndex] = useState(0);
    const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);

    const updateWindowDimensions = () => {
        setIsMobile(window.innerWidth <= 768);
    };

    useEffect(() => {
        window.addEventListener('resize', updateWindowDimensions);
        return () => window.removeEventListener('resize', updateWindowDimensions);
    }, []);

    const images = isMobile
        ? [
            'https://tuneapp.blackbirdagency.ru/image/cache/catalog/demo/banners/banner-small2-708x240.jpg',
            'https://tuneapp.blackbirdagency.ru/image/cache/catalog/demo/banners/banner-small1-708x240.jpg'
        ]
        : [
            'https://tuneapp.blackbirdagency.ru/image/cache/catalog/frame2087324724%281%29-1440x320.png',
            'https://tuneapp.blackbirdagency.ru/image/cache/catalog/demo/banners/banner-carousel-1440x320.jpg',
        ];

    const desktopSliderStyle = {
        maxHeight: isMobile ? '450px' : '100%',
        marginTop: '30px',
        borderRadius: '30px',
    };

    const imageStyle = {
        objectFit: 'contain',
        maxHeight: isMobile ? '360px' : '100%',
        width: '100%',
        borderRadius: '30px',
    };

    return (
        <div className="slider-container" style={desktopSliderStyle}>
            <Carousel activeIndex={index} onSelect={setIndex} indicators={false}>
                {images.map((image, idx) => (
                    <Carousel.Item key={idx}>
                        <img className="d-block w-100" src={image} alt={`Slide ${idx + 1}`} style={imageStyle} />
                    </Carousel.Item>
                ))}
            </Carousel>
            <div style={{ display: 'flex', justifyContent: 'center', marginTop: '10px' }}>
                {images.map((_, idx) => (
                    <div
                        key={idx}
                        style={{
                            width: '10px',
                            height: '10px',
                            borderRadius: '50%',
                            margin: '0 5px',
                            cursor: 'pointer',
                            background: idx === index ? '#ff0000' : '#fcb4b4',
                        }}
                        onClick={() => setIndex(idx)}
                    />
                ))}
            </div>
        </div>
    );
};

export default Slider;
