import IndexPage from "../pages/IndexPage";
import {CatalogPage} from "../pages/CatalogPage";
import {DetailPage} from "../pages/DetailPage";
import {CartPage} from "../pages/CartPage";
import {OrderPage} from "../pages/OrderPage";


export const publicRoutes = [
    {path: '/', component: IndexPage, exact: true},
    {path: '/catalog', component: CatalogPage, exact: true},
    {path: '/catalog/detail/:subcategory_name/:id', component: DetailPage, exact: true},
    {path: '/cart', component: CartPage, exact: true},
    {path: '/order', component: OrderPage, exact: true},
    {path: '*', component: IndexPage, exact: true},
]

export const privateRoutes = [
    {path: '/', component: IndexPage, exact: true},
    {path: '/catalog', component: CatalogPage, exact: true},
    {path: '/catalog/detail/:subcategory_name/:id', component: DetailPage, exact: true},
    {path: '/cart', component: CartPage, exact: true},
    {path: '/order', component: OrderPage, exact: true},
    {path: '*', component: IndexPage, exact: true},
]

