import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Offcanvas from 'react-bootstrap/Offcanvas';
import {FormControl} from "react-bootstrap";

import {Link} from "react-router-dom";

import {useContext, useEffect, useState} from "react";

import {CartContext, SelectedWordContext} from "../../context";

import './NavBar.css';
import {colors} from "@material-ui/core";


const NavBar = () => {
    const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);
    const {selectedWord, setSelectedWord} = useContext(SelectedWordContext);
    const { cartItems } = useContext(CartContext);
    const totalItems = cartItems.reduce((total, item) => total + item.quantity, 0);

    // Функция для обновления размеров окна
    const updateWindowDimensions = () => {
        setIsMobile(window.innerWidth <= 768);
    };

    // Эффект для добавления слушателя событий при монтировании компонента
    useEffect(() => {
        // Попытка получить сохраненное значение из локального хранилища
        const savedSelectedWord = localStorage.getItem('selectedWord');
        if (savedSelectedWord) {
            setSelectedWord(parseInt(savedSelectedWord));
        }

        // Добавление слушателя событий для обновления размеров окна
        window.addEventListener('resize', updateWindowDimensions);

        // Функция, которая будет вызвана при размонтировании компонента
        return () => {
            // Удаление слушателя событий при размонтировании компонента
            window.removeEventListener('resize', updateWindowDimensions);
        };
    }, [setSelectedWord]); // Пустой массив зависимостей означает, что эффект выполнится только при монтировании и размонтировании компонента

    // Обработчик клика по слову в навигационной панели
    const handleWordClick = (index) => {
        setSelectedWord(index);
        // Сохранение выбранного индекса в локальное хранилище
        localStorage.setItem('selectedWord', index.toString());
    };


    const logo = 'https://tuneapp.blackbirdagency.ru/_html/img/logo.svg'
    const cartIcon = 'https://s3.timeweb.com/2481cb39-1f5a3cd1-6620-459a-860f-6d8d44288631/ico/icons8-cart-48.png'
    const loveIcon = 'https://s3.timeweb.com/2481cb39-1f5a3cd1-6620-459a-860f-6d8d44288631/ico/icons8-love-100.png'
    const loginIcon = 'https://s3.timeweb.com/2481cb39-1f5a3cd1-6620-459a-860f-6d8d44288631/ico/icons8-login-100.png'
    const words = [
        "Смартфоны",
        "Планшеты",
        "Часы",
        "Ноутбуки",
        "Наушники",
        "Аксессуары",
        "Красота",
        "Квадрокоптеры",
        "Игровые приставки"
    ];

    return (
        <div style={{marginBottom: '20px'}}>
            <Navbar key={'xl'} expand={'xl'} className="mb-1 navbar-xl" style={{ height: '44px', overflow: 'hidden', color: 'black', }}>
                <Container fluid style={{padding: 0}}>
                    <Navbar.Brand href="#" className="me-auto">Санкт-Петербург</Navbar.Brand> {/* Используйте класс "me-auto" для выравнивания слева */}
                    <Navbar.Toggle aria-controls={`offcanvasNavbar-expand-xl`} />
                        <Offcanvas.Body>
                            <Nav className="justify-content-end flex-grow-1 text-center">
                                <Nav.Link href="#action1" className={'header1_link'} >Трейд-ин</Nav.Link>
                                <Nav.Link href="#action1" className={'header1_link'} >Ремонт</Nav.Link>
                                <Nav.Link href="#action2" className={'header1_link'} >Доставка и оплата</Nav.Link>
                                <Nav.Link href="#action2" className={'header1_link'} >Контакты</Nav.Link>
                            </Nav>
                        </Offcanvas.Body>
                </Container>
            </Navbar>

            <Navbar  expand="lg" style={isMobile ? {display: 'none'} : {}}>
                <Navbar.Brand href="/">
                    <img
                        src={logo}
                        width="110"
                        height="31"
                        className="d-inline-block align-top"
                        alt="Логотип"
                    />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Form className="mx-auto" style={{ width: '100%', display: 'flex', flexDirection: 'row' }}>
                        <FormControl type="text" placeholder="Искать" className="mr-sm-2" style={{ flex: '1' }} />
                        <Button style={{ marginLeft: '5px', background: '#ff0000', border: 'none', }}><b>Найти</b></Button>
                    </Form>

                    <Nav className="justify-content-end flex-grow-1 text-center" >
                        <Nav.Link href="#chat1" className={'header2_btn'}>
                            <img src={loveIcon} width="24" height="24" className="d-inline-block align-top" alt="Чат 1" />
                            Избранное
                        </Nav.Link>
                        <Nav.Link href="" className={'header2_btn'} style={{ position: 'relative' }}>
                            <Link to={`/cart`} style={{color: 'black'}}>
                                <img src={cartIcon} width="24" height="24" className="d-inline-block align-top" alt="Чат 2" />
                                {totalItems > 0 && (
                                    <span className="cart-counter" style={{ position: 'absolute', top: '0', right: '0', background: 'red', borderRadius: '50%', padding: '5px 8px', color: 'white', fontSize: '12px' }}>
                                {totalItems}
                        </span>
                            )}
                            Корзина
                            </Link>
                        </Nav.Link>
                        <Nav.Link href="#chat3" className={'header2_btn'}>
                            <img src={loginIcon} width="24" height="24" className="d-inline-block align-top" alt="Чат 3" />
                            Войти
                        </Nav.Link>
                    </Nav>

                </Navbar.Collapse>
            </Navbar>
            <div className="word-list">
                {words.map((word, index) => {
                    const argument = word.toLowerCase().replace(' ', '');
                    return (
                        <Link
                            key={index}
                            to={`/catalog?type=${argument}`}
                            className={`word-link ${selectedWord === index ? 'selected' : ''}`}
                            onClick={() => handleWordClick(index)}
                        >
                            {word}
                        </Link>
                    );
                })}
            </div>
            <Navbar style={isMobile ? {} : {display: 'none'}}>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Form className="mx-auto" style={{ width: '100%', display: 'flex', flexDirection: 'row' }}>
                        <FormControl type="text" placeholder="Искать" className="mr-sm-2" style={{ flex: '1' }} />
                        <Button style={{ marginLeft: '5px', background: '#ff0000', border: 'none' }}>Найти</Button>
                    </Form>
                </Navbar.Collapse>
            </Navbar>

        </div>
    );
};
export default NavBar;
