import {Link, useParams} from 'react-router-dom';
import React, {useContext, useEffect, useMemo, useState} from "react";
import { NewProductApi } from "../API/v_1/newProduct";
import {Carousel, Container, Row, Col, Form, Card, Button} from 'react-bootstrap';
import {CirclePicker} from "react-color";
import {colorMapping} from "./hex";
import './css/DetailPage.css'
import {CartContext} from "../context";
import {Cart} from "../components/Service/Cart/Cart";
import {ToCart} from "../components/Cart/ToCart";

export const DetailPage = () => {
    let { id, subcategory_name } = useParams();
    const newProductApi = useMemo(() => new NewProductApi(), []);
    const [products, setProducts] = useState(null);
    const [desiredProduct, setDesiredProduct] = useState(null);
    const [index, setIndex] = useState(0);
    const [selectedColorBlock, setSelectedColorBlock] = useState(null);
    const [selectedColor, setSelectedColor] = useState(desiredProduct?.params.find(param => param.name === 'Цвет')?.value || null);
    const { cartItems, addToCart, removeFromCart, clearCart } = useContext(CartContext);

    useEffect(() => {
        newProductApi
            .get({
                subcategory_name: subcategory_name,
            })
            .then((data) => {
                console.log('Полученные данные новых товаров:', data);
                setProducts(data.data.items);

                const newDesiredProduct = data.data.items.find((product) => product.id === parseInt(id));
                setDesiredProduct(newDesiredProduct);


            })
            .catch((error) => {
                console.error('Ошибка при выполнении запроса:', error);
            });
    }, [ id, newProductApi, subcategory_name]);
    useEffect(() => {

        const pw = desiredProduct?.params.find(param => param.name === 'Цвет');
        setSelectedColor(pw?.value);

    }, [desiredProduct?.params, selectedColor]);

    const filteredProducts = useMemo(() => {
        return products && desiredProduct ? products.filter(product =>
            compareParams(product.params, desiredProduct.params)
        ) : [];
    }, [products, desiredProduct]);
    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    function compareParams(productParams, desiredParams) {
        return desiredParams.every(desiredParam => {
            // Исключаем сравнение по цвету
            if (desiredParam.name === "Цвет") {
                return true;
            }
            // Сравниваем параметры
            const matchingParam = productParams.find(productParam =>
                productParam.name === desiredParam.name && productParam.value === desiredParam.value
            );
            return !!matchingParam;
        });
    }

    // Фильтрация продуктов

    const handleColorSelect = (color) => {
        setSelectedColor(color);

        // Найдем товар с выбранным цветом в отфильтрованных продуктах
        const filteredProduct = filteredProducts.find(product =>
            product.params.some(param => param.name === 'Цвет' && param.value.toLowerCase() === color.toLowerCase())
        );
        // Если товар найден, обновим выбранный продукт и индекс слайда
        // В противном случае используем предыдущий выбранный продукт
        setDesiredProduct(filteredProduct || desiredProduct);
        setIndex(0);
    };


    return (
        <Container fluid md={4} lg={5} xl={5} xxl={1} style={{margin: '0 auto'}} className='containerDetail'>
            <div>
                <h3>{desiredProduct?.name}</h3>
            </div>
            <Row xl={4} xxl={9} style={{
                display: 'flex',
            }}>
                <Col md={4} lg={5} xl={1} className="d-none d-md-block" >
                    {/* Miniature Images */}
                    {/* Map over the images in the desiredProduct */}
                    {desiredProduct && desiredProduct.images.map((thumb, idx) => (
                        <img
                            id='imgPCMini'
                            key={idx}
                            src={thumb}
                            alt={`Thumbnail ${idx + 1}`}
                            onClick={() => setIndex(idx)}
                            style={{ maxWidth: '100px', maxHeight: '100px' }}
                        />
                    ))}
                </Col>
                <Col md={1} lg={4} xl={5}>
                    <Carousel activeIndex={index} onSelect={handleSelect}>
                        {/* Map over the images in the desiredProduct */}
                        {desiredProduct && desiredProduct.images.map((image, idx) => (
                            <Carousel.Item key={idx}>
                                <img
                                    className="d-block w-100"
                                    src={image}
                                    alt={`Slide ${idx + 1}`}
                                />
                            </Carousel.Item>
                        ))}
                    </Carousel>
                    {/* Miniature Images for mobile */}
                    <div className="d-sm-block d-md-none">
                        {desiredProduct && desiredProduct.images.map((thumb, idx) => (
                            <img
                                key={idx}
                                src={thumb}
                                alt={`Thumbnail ${idx + 1}`}
                                onClick={() => setIndex(idx)}
                                style={{ maxWidth: '100px', maxHeight: '100px' }}
                            />
                        ))}
                    </div>
                </Col>
                {/* Новый блок с фильтрами */}
                <Col md={3} lg={3} xl={3} >
                    <div>
                        {/* Фильтр по цвету */}
                        <p>Цвет:</p>
                        <div className='colorFilter'>
                            {filteredProducts && (
                                <div style={{ display: 'flex', flexWrap: 'nowrap' }} className='colorFilter' >
                                    {Array.from(new Set(filteredProducts.flatMap(product => product.params
                                        .filter(param => param.name === 'Цвет' && param.is_filter)
                                        .map(param => colorMapping[param.value])
                                        .filter(hex => hex)


                                    ))).map((color, index) => (
                                        <div className='colorBlock' key={index}
                                             style=
                                                 {{

                                                     border:
                                                         (selectedColorBlock === index) || selectedColor === Object.entries(colorMapping)
                                                         .find(([name, hex]) => hex.toLowerCase() === color.toLowerCase())?.[0]
                                                             ? '2px solid red' : '2px solid #E0E0E0',
                                                 }}>
                                            <div
                                                className={`colorItem`}
                                                style={{
                                                    width: '32px',
                                                    height: '32px',
                                                    borderRadius: '50%',
                                                    backgroundColor: color,
                                                    cursor: 'pointer',
                                                    display: 'flex',

                                                }}
                                                onClick={() => {
                                                    const colorName = Object.entries(colorMapping)
                                                        .find(([name, hex]) => hex.toLowerCase() === color.toLowerCase())?.[0];
                                                    handleColorSelect(colorName);
                                                    setSelectedColorBlock(index);
                                                    console.log(colorName)
                                                }}
                                            ></div>
                                        </div>
                                    ))}

                                </div>
                            )}
                        </div>


                        {/* Другие фильтры (если есть) */}
                        {/* ... */}
                    </div>
                </Col>

                <Col md={3} lg={3} xl={3} xxl={3} id='block_3'>
                    {/* Блок 200x300 с фоном #FFF5F5 и ценой */}
                    <div className='productInfo'>
                        {/* Ваш код для отображения цены здесь */}
                        <div>
                            <h3><b>{desiredProduct?.amount} ₽</b></h3>
                        </div>
                        <div>
                            <img
                                className='imgBox'
                                src='https://s3.timeweb.com/2481cb39-1f5a3cd1-6620-459a-860f-6d8d44288631/ico/check-mark.png'
                                alt='box'
                            />
                            В наличии: {desiredProduct?.quantity} шт.
                        </div>
                        <div style={{fontSize: '0.8em'}}>
                            <div style={{ marginTop: '20px', width: '100%' }}>
                                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                                    <p>При оплате картой</p>
                                    <p>{desiredProduct?.amount} ₽</p>
                                </div>
                            </div>

                            <div style={{ marginTop: '10px', width: '100%' }}>
                                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                                    <p>При безналичном расчете</p>
                                    <div style={{ display: 'flex', alignItems: 'center' }}>
                                        <div style={{ marginLeft: '5px', borderBottom: '1px dashed #000', flex: '1' }}></div>
                                        <p style={{ marginLeft: '5px' }}>{(desiredProduct?.amount * 1.1).toFixed(2)} ₽ </p>
                                    </div>
                                </div>
                            </div>
                            <ToCart product={desiredProduct} showDetail={false}/>
                        </div>
                    </div>
                </Col>
            </Row>

        </Container>

    );
};