import React, {useContext} from "react";
import {CartContext} from "../../context";
import {Button, Card} from "react-bootstrap";
import {Link} from "react-router-dom";
import {Cart} from "../Service/Cart/Cart";

export const ToCart = ({product, showDetail=true}) => {
    const { cartItems, addToCart } = useContext(CartContext);
    return(
        <>
            {cartItems.some(item => item.id === product?.id) ? (
                <Card.Body>
                    {
                        showDetail ? (
                            <Link to={`/catalog/detail/${product?.subcategory.name}/${product?.id}`} style={{ color: "black" }}>
                                <Card.Text className="productAmount"><b>{product?.amount.toLocaleString()} ₽</b></Card.Text>
                                <Card.Title className="productName">{product?.name}</Card.Title>
                            </Link>
                        )
                            : (
                                <></>
                            )
                    }

                    <Cart cartItem={cartItems.find(item => item.id === product?.id)} />
                </Card.Body>
            ) : (
                <Card.Body>
                    {
                        showDetail ? (
                                <Link to={`/catalog/detail/${product?.subcategory?.name}/${product?.id}`} style={{ color: "black" }}>
                                    <Card.Text className="productAmount"><b>{product?.amount?.toLocaleString()} ₽</b></Card.Text>
                                    <Card.Title className="productName">{product?.name}</Card.Title>
                                </Link>
                            )
                            : (
                                <></>
                            )
                    }

                    <Button
                        onClick={() => addToCart(product)}
                        style={{
                            width: '100%',
                            background: '#ff0000',
                            border: 'none',
                            height: '45px'
                        }}
                    >В корзину</Button>
                </Card.Body>
            )}
        </>
    )
}