import React, {useState, useMemo, useEffect, useContext} from 'react';
import { Row, Col, Card, Pagination, Form, Button, Modal } from 'react-bootstrap';
import {Link, useLocation} from "react-router-dom";
import {NewProductApi} from "../API/v_1/newProduct";
import './css/Catalog.css'
import {Cart} from "../components/Service/Cart/Cart";
import {CartContext} from "../context";
import {ToCart} from "../components/Cart/ToCart";


export const CatalogPage = () => {
    const newProductApi = useMemo(() => new NewProductApi(), []);
    const location = useLocation();
    const [products, setProducts] = useState(null);
    const [typeParam, setTypeParam] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(6);
    const [filters, setFilters] = useState({});

    const [showFiltersModal, setShowFiltersModal] = useState(false);
    const { cartItems, addToCart, removeFromCart, clearCart } = useContext(CartContext);


// Обработчик добавления товара в корзину
    const handleAddToCart = (product) => {
        addToCart(product);
    };

    useEffect(() => {
        const queryParams = new URLSearchParams(location.search);
        const type = queryParams.get('type');
        if (type !== typeParam) {
            setTypeParam(type);

            newProductApi.get({
                type_name: type
            })
                .then(data => {
                    // Обработка полученных данных
                    console.log('Полученные данные новых товаров:', data);
                    setProducts(data.data.items);
                })
                .catch(error => {
                    // Обработка ошибок
                    console.error('Ошибка при выполнении запроса:', error);
                });
        }

    }, [newProductApi, location.search, typeParam]);

    // Функция для фильтрации товаров
    const filteredProducts = useMemo(() => {
        if (products) {
            let filtered = products;
            for (const key in filters) {
                if (filters[key] && filters[key].length > 0) {
                    // Фильтруем товары, которые содержат хотя бы одно из выбранных значений фильтра
                    filtered = filtered.filter(product =>
                        product.params.some(param =>
                            param.name === key && filters[key].includes(param.value)
                        )
                    );
                }
            }
            return filtered;
        }
        return [];
    }, [products, filters]);

    // Вычисление максимального количества страниц
    const totalPages = useMemo(() => {
        if (filteredProducts) {
            return Math.ceil(filteredProducts.length / itemsPerPage);
        }
        return 0;
    }, [filteredProducts, itemsPerPage]);
    if (products === null) {
        return <div>Loading...</div>; // или любой другой компонент загрузки
    }
    // Вычисление индексов начала и конца отображаемых товаров
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredProducts.slice(indexOfFirstItem, indexOfLastItem);

    // Изменение страницы
    const paginate = pageNumber => setCurrentPage(pageNumber);

    // Обработчик изменений в фильтрах

    const handleCheckboxChange = event => {
        const { name, value, checked } = event.target;
        if (checked) {
            // Добавляем выбранное значение в фильтры
            setFilters(prevFilters => ({ ...prevFilters, [name]: [...(prevFilters[name] || []), value] }));
        } else {
            // Удаляем выбранное значение из фильтров
            setFilters(prevFilters => ({
                ...prevFilters,
                [name]: (prevFilters[name] || []).filter(item => item !== value)
            }));
        }
        setCurrentPage(1); // Сбросить страницу при изменении фильтров
    };



    return (
        <Row>
            <Col sm={3} className="d-none d-sm-block">
                <Form>
                    {/* Форма для фильтров */}
                    {/* Для каждого уникального параметра создаем фильтр */}
                    {products.reduce((acc, product) => {
                        product.params.forEach(param => {
                            if (param.is_filter && !acc.includes(param.name)) {
                                acc.push(param.name);
                            }
                        });
                        return acc;
                    }, []).map((param, index) => (
                        <Form.Group controlId={`filter${index}`} key={param}>
                            <Form.Label>{param}</Form.Label>
                            <div>
                                {/* Используйте группу чекбоксов вместо выпадающего списка */}
                                {[...new Set(products.flatMap(product => product.params
                                    .filter(p => p.name === param && p.is_filter)
                                    .map(p => p.value)))].map(value => (
                                    <Form.Check
                                        key={value}
                                        type="checkbox"
                                        id={value}
                                        label={value}
                                        name={param}
                                        value={value}
                                        checked={filters[param] && filters[param].includes(value)}
                                        onChange={handleCheckboxChange}
                                    />
                                ))}
                            </div>
                        </Form.Group>
                    ))}
                </Form>
            </Col>
            <Col sm={3} className="d-sm-none">
                <Button onClick={() => setShowFiltersModal(true)}>Фильтры</Button>
                <Modal show={showFiltersModal} onHide={() => setShowFiltersModal(false)}>
                    <Modal.Header closeButton>
                        <Modal.Title>Фильтры</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            {/* Форма для фильтров */}
                            {/* Для каждого уникального параметра создаем фильтр */}
                            {products.reduce((acc, product) => {
                                product.params.forEach(param => {
                                    if (param.is_filter && !acc.includes(param.name)) {
                                        acc.push(param.name);
                                    }
                                });
                                return acc;
                            }, []).map((param, index) => (
                                <Form.Group controlId={`filter${index}`} key={param}>
                                    <Form.Label>{param}</Form.Label>
                                    <div>
                                        {/* Используйте группу чекбоксов вместо выпадающего списка */}
                                        {[...new Set(products.flatMap(product => product.params
                                            .filter(p => p.name === param && p.is_filter)
                                            .map(p => p.value)))].map(value => (
                                            <Form.Check
                                                key={value}
                                                type="checkbox"
                                                id={value}
                                                label={value}
                                                name={param}
                                                value={value}
                                                checked={filters[param] && filters[param].includes(value)}
                                                onChange={handleCheckboxChange}
                                            />
                                        ))}
                                    </div>
                                </Form.Group>
                            ))}
                        </Form>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => setShowFiltersModal(false)}>
                            Закрыть
                        </Button>
                        <Button variant="primary" onClick={() => {
                            setShowFiltersModal(false);
                            setCurrentPage(1); // Сбросить страницу при изменении фильтров
                        }}>
                            Применить
                        </Button>
                    </Modal.Footer>
                </Modal>
            </Col>


            <Col sm={9}>
                <Row>
                    {currentItems.map(product => (
                        <Col key={product.id} lg={3} md={6} sm={12} style={{ maxWidth: '240px' }}>
                            <Card className="mb-3">
                                <Link to={`/catalog/detail/${product.subcategory.name}/${product.id}`} style={{color: "black"}}>
                                    <Card.Img variant="top" src={product.images[0]} style={{ maxHeight: '250px', objectFit: 'cover' }} />
                                </Link>
                                <ToCart product={product}/>
                            </Card>
                        </Col>
                    ))}
                </Row>

                {/* Пагинация */}
                <Pagination className="justify-content-center">
                    {Array.from({ length: totalPages }, (_, index) => (
                        <Pagination.Item key={index + 1} active={index + 1 === currentPage} onClick={() => paginate(index + 1)}>
                            {index + 1}
                        </Pagination.Item>
                    ))}
                </Pagination>
            </Col>
        </Row>
    );
};
